@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;1,100;1,300;1,400;1,500;1,700&display=swap');
@import url('https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css');

.App {
  text-align: center;
  margin-top:100px;
  margin-bottom: 100px;
  font-family: 'Roboto';
  padding: 0 !important;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.menu div{ 
    cursor: pointer;
    border-bottom:1px solid rgba(255, 255, 255, 0.1);
  }

  .col-12 h1{
    font-size: 46px;
    font-weight: 700;
  }

  .col-12 hr{   
   border-top: 1px solid  rgba(255,255,255,0.2);
   margin:0 50px 10px 50px;
  }

   .col-12 h2{
    /* font-size: 46px; */
    font-weight: 700;
  }

  @media only screen and (min-device-width : 769px) 
  and (max-device-width : 2560px)  {
  .sidebar{    
    position: absolute;
    height: 100%;
    background-image: linear-gradient(to right, rgba(29,31,32 ,1),rgba(29,31,32 ,0.8),rgba(29,31,32 ,0.6));
    width: 50px;
    top: 0; 
    left: 0;
    display: flex
  }
}

  .content{
    width: '100%'; height: '80vh'
  }

  @media only screen and (max-width: 768px) {
   .sidebar{
     height: none !important;
     background-color: white;
     /* background-image: linear-gradient(to right, rgb(29, 31, 32), rgba(29, 31, 32, 0.8), rgba(29, 31, 32, 0.6)); */
     width: 100%;     
     display: flex;
     bottom: 0;
     position: fixed;
     overflow: hidden;
     z-index: 2;
   }

   .sidebar .menu{
     display: flex;
     margin:0 !important;
     justify-content: center;
   }

   .sidebar .menu div{
    padding-right:50px;
    border:none;
    margin-bottom:15px;
   }

   .sidebar .menu div i{
     color: black !important;
   }

   .content .inner-content img{
    width: 100% !important;

   }

   .content .inner-content{
    display: block !important ;
    margin:0 !important;
   }
}
  


